button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: inherit;
}
input,
textarea {
  font-weight: inherit;
  background-color: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  &::placeholder {
    font-size: inherit;
  }
  &:-webkit-autofill,
  &:-webkit-autofill:focus {
    transition: background-color 600000s 0s, color 600000s 0s;
  }
}
textarea {
  display: block;
}
a {
  color: inherit;
}
th {
  text-align: left;
  font-weight: inherit;
}
[data-spoiler] {
  cursor: pointer;
}
[class*="image-sprite"] {
  height: 0;
  width: 0;
  visibility: hidden;
}
._accent {
  color: $accentBlue;
}
*::before, *::after {
  user-select: none;
  pointer-events: none;
}
img {
  height: auto;
  &:not([src]) {
    visibility: hidden;
  }
}
.swiper-autoheight {
  height: auto;
  .swiper-slide {
    height: auto;
  }
 .swiper-wrapper {
    align-items: flex-start;
    transition-property: transform, height;
  }
}
.swiper-wrapper {
  display: flex;
}
.swiper-3d {
  perspective: 1200px;
  .swiper-wrapper {
    transform-style: preserve-3d;
  }
}
.swiper-button-next,
.swiper-button-prev {
  position: absolute;
  top: 50%;
  @include size(50);
  flex-shrink: 0;
  border-radius: 50%;
  transition: transform .3s, background-color .3s, opacity .3s;
  background-color: $sliderPaginationColor;
  box-shadow: 0px 9px 19px rgba(0, 0, 0, 0.15);
  cursor: pointer;
  &:not(.swiper-button-disabled) {
    @media (any-hover: hover) {
      &:hover {
        box-shadow: 0px 9px 19px rgba(0, 0, 0, 0.15),
          0px 0px 15px rgba(#1858cd, 0.3);
      }
    }
  }

  @include flex-center;
  &::before {
    content: "\e906";
    @extend %ic;
    color: $white;
  }
}
.swiper-button-next {
  right: 0;
  transform: translate(50%, -50%);
  &::before {
    transform: rotate(180deg);
  }
}
.swiper-button-prev {
  left: 0;
  transform: translate(50%, -50%);
}
.swiper-button-disabled {
  cursor: not-allowed;
  background-color: $sliderLightButtonColor;
}
._section-title {
  text-transform: uppercase;
  @include aminvalue("font-size", 56, 37, $phone);
  font-weight: 700;
  position: relative;
  line-height: 1;
  @media (max-width: $phone + px) {
    line-height: 1.1;
    padding-left: rem(15);
    padding-right: rem(15);
  }
  @media (max-width: $smallPhone + px) {
    font-size: rem(27);
    line-height: math.div(37, 27);
  }
}
::selection {
  background-color: rgba(#616bd9, .5);
}
.button {
  border: 2px solid $mainBlue;
  filter: drop-shadow(0px 9px 19px rgba(0, 0, 0, 0.15));
  background: transparent;
  padding: rem(12) rem(20);
  border-radius: rem(50);
  text-align: center;
  &::before {
    content: "";
    transition: opacity 0.3s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    line-height: math.div(30, 16);
    border-radius: rem(50);
    background-image: linear-gradient(
      122.93deg,
      #7345d3 -137.34%,
      #5470f0 38.32%,
      #1858cd 129.02%
    );
    z-index: -1;
    opacity: 0;
  }
  @media (any-hover: hover) {
    &:hover {
      &::before {
        opacity: 1;
      }
    }
  }
  @media (max-width: $phone + px) {
    font-size: rem(14);
    width: rem(180);
  }
  &--highlighted {
    color: #fff;
    font-weight: 600;
    box-shadow: 0px 9px 19px rgba(0, 0, 0, 0.15);
    position: relative;
    z-index: 1;
    &::before {
      opacity: 1;
    }
    @media (any-hover: hover) {
      &:hover {
        &::before {
          opacity: 0;
        }
      }
    }
  }
}
._decor-hexagon {
  position: relative;
  white-space: nowrap;
  display: inline-block;
  &::before, &::after {
    background: center / contain no-repeat var(--hexagon);
    display: inline-block;
    position: absolute;
    top: 50%;
    width: 71px;
    height: pc(58, 42);
    z-index: -1;
    @media (max-width: 768px) {
      background: center / contain no-repeat var(--hexagon--thickest);
      width: 63px;
    }
    @media (max-width: 480px) {
      width: 47px;
      height: pc(34, 32);
      height: 140%;
    }
  }
  &._dark-bg {
    &::before, &::after {
      background-image: var(--hexagon-thin-dark-bg);
    }
  }
  &._left {
    &::before {
      content: '';
      left: 0;
      transform: translate(-30%, -50%);
    }
  } 
  &._right {
    &::after {
      content: '';
      right: 0;
      transform: translate(30%, -50%);
    }
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.header-burger-button {
  opacity: 0.8;
  font-weight: 600;
  display: flex;
  align-items: center;
  font-size: inherit;
  line-height: inherit;
  height: 24px;
  white-space: nowrap;
  .burger-lines {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 34px;
    height: 21px;
    span {
      height: 1px;
      opacity: 1;
      width: 34px;
      background-color: #fff;
      &:nth-child(2) {
        width: 26px;
      }
      &:nth-child(3) {
        width: 30px;
      }
    }
  }
  .burger-text {
    margin-left: 5px;
  }
}
span._responsive, span.responsive {
  display: none;
}
._icon-list {
  display: flex;
  gap: rem(15);
  color: $mainBlue;
  &__item {
  }
  &__link {
    border-radius: 15px;
    width: rem(45);
    height: rem(45);
    @include flex-center;
    background-color: rgba(255, 255, 255, 0.15);
    transition: background-color .3s;
    box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.12);
    border-radius: 50%;
    font-size: 18px;
    @media (any-hover: hover) {
      &:hover {
        background-color:  rgba($lightBlue, .28);
      }
    }
  }
}
.nobr {
  white-space: pre;
}
._ibg {
  position: relative;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}
.row:after {
  display: block;
  content: "";
  clear: both;
}
.rub:after {
  content: "\u20BD";
}
%noselect {
  user-select: none;
}
