.learning {
  margin-top: rem(82);
  padding-bottom: rem(40 + 200);
  position: relative;
  overflow: hidden;
  @media (max-width: $phone + px) {
    padding-bottom: rem(100);
    margin-top: rem(50);
  }
  @media (max-width: $smallPhone + px) {
    margin-top: rem(30);
    padding-bottom: rem(20 + 100);
  }
  &::before {
    content: "";
    position: absolute;
    @include size(174, 633);
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background: no-repeat url(@img/learning/learning-dots.png);
    @media (max-width: $maxWidthContainer - 1 + px) {
      display: none;
    }
    @media (min-width: #{$maxWidth + 1}px) {
      display: none;
    }
  }
  &::after {
    content: "";
    position: absolute;
    @include size(47, 42);
    right: rem(30);
    top: rem(267);
    background: center / contain no-repeat var(--hexagon-thick-empty);
    @media (max-width: $maxWidthContainer - 1 + px) {
      display: none;
    }
  }
  // .learning__container
  &__container {
    position: relative;
  }
  &__bg {
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 100%;
    height: 412px;
    transform: translate(-50%, 0);
    max-width: 1720px;
    @media (max-width: 768px) {
      bottom: -20%;
    }
  }
  // .learning__header
  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: rem(59);
    gap: rem(12) rem(70);
    @media (max-width: $phone + px) {
      column-gap: rem(70);
      margin-bottom: rem(28);
    }
    @media (max-width: 600 + px) {
      flex-direction: column;
    }
  }
  // .learning__title
  &__title {
    margin-top: rem(21);
    @media (max-width: 768px) {
    ._decor-hexagon._left {
        &::before {
         display: none;
        }
      }
    }
    @media (min-width: 769px) {
    ._decor-hexagon._right {
        &::before {
          display: none;
        }
      }
    }
    @media (max-width: 600px) {
      text-align: center;
    }
  }
  // .learning__sub-title
  &__sub-title {
    font-weight: 500;
    @include aminvalue("font-size", 22, 17, $phone);
    line-height: math.div(37, 22);
    @include aminvalue("padding-top", 32, 20, $phone);
    @include aminvalue("padding-bottom", 32, 20, $phone);
    @include aminvalue("padding-left", 45, 25, $phone);
    @include aminvalue("padding-right", 45, 25, $phone);
    background: rgba($white, 0.95);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
    backdrop-filter: blur(30px);
    border-radius: 15px;
    @media (max-width: 600px) {
      margin-top: 15px;
      margin-bottom: 15px;
    }
    @media (max-width: $phone + px) {
      line-height: math.div(26, 17);
    }
  }
  // .learning__content
  &__content {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      right: rem(-48);
      top: 53%;
      height: 53%;
      width: 1px;
      border-right: 1px dashed $mainBlue;
      transform: translateY(-100%);
      @media (max-width: $phone + px) {
        top: 45%;
        right: auto;
        left: pc(768 - 46 + 15, 768);
        height: 45%;
      }
      @media (max-width: $smallPhone + px) {
        display: none;
      }
    }
    &::after {
      content: "";
      position: absolute;
      right: rem(-48);
      top: 0;
      height: 1px;
      width: 100vw;
      border-top: 1px dashed $mainBlue;
      transform: translateX(100%);
      @media (max-width: $phone + px) {
        right: auto;
        left: pc(768 - 46 + 15, 768);
        top: 0;
        transform: none;
      }
      @media (max-width: $smallPhone + px) {
        display: none;
      }
    }
  }
  // .learning__content-title
  &__content-title {
    margin-bottom: rem(58);
    font-size: rem(32);
    line-height: math.div(37, 32);
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    @media (max-width: $phone + px) {
      font-size: rem(24);
      margin-bottom: rem(31);
    }
    @media (max-width: $smallPhone + px) {
      margin-bottom: 0;
      font-size: rem(18);
    }
  }
  // .learning__reason-list
  &__reason-list {
    display: flex;
    justify-content: space-between;
    gap: rem(30) rem(30);
    @media (max-width: $smallPhone + px) {
      flex-direction: column;
      row-gap: 0;
    }
  }
  // .learning__reason-item
  &__reason-item {
  }
  & ._dashed-line {
    position: absolute;
    top: 53%;
    right: rem(-48);
    width: 150vw;
    height: 1px;
    border-top: 1px dashed $mainBlue;
    z-index: 10;
    @media (max-width: $phone + px) {
      top: 45%;
      margin-left: rem(-15);
      left: 0;
      right: auto;
      width: pc(768 - 46 + 30, 768);
    }
    @media (max-width: $smallPhone + px) {
      display: none;
    }
  }
}

.reason {
  @include flex-center;
  flex-direction: column;
  padding-top: rem(14);
  z-index: 11;
  ._dashed-line {
    display: none;
  }
  @media (max-width: $phone + px) {
    padding-top: rem(10);
  }
  @media (max-width: $smallPhone + px) {
    max-width: pc($smallPhone - 8 * 2, $smallPhone);
    padding-left: rem(14);
    padding-right: rem(14);
  }
  @media (max-width: $smallPhone + px) {
    position: relative;
    padding-top: rem(30);
    ._dashed-line {
      position: absolute;
      margin-left: 0;
      top: 0;
      height: 43%;
      width: 1px;
      border-right: 1px dashed $mainBlue;
      display: block;
    }
    &:nth-child(even) {
      ._dashed-line {
        left: 0;
      }
    }
    &:nth-child(odd) {
      ._dashed-line {
        right: 0;
        left: auto;
      }
    }
    &::before,
    &::after {
      content: "";
      position: absolute;
    }
    &:nth-child(odd) {
      &::before {
        left: 0;
        top: 43%;
        width: 100%;
        height: 1px;
        border-top: 1px dashed $mainBlue;
      }
      &::after {
        top: 43%;
        left: 0;
        width: 1px;
        height: 100% - 43%;
        border-right: 1px dashed $mainBlue;
      }
    }
    &:nth-child(even) {
      &::before {
        left: 0;
        top: 43%;
        width: 100%;
        height: 1px;
        border-top: 1px dashed $mainBlue;
      }
      &::after {
        top: 43%;
        left: auto;
        right: 0;
        width: 1px;
        height: 100% - 43%;
        border-right: 1px dashed $mainBlue;
      }
    }
    &:first-child,
    &:last-child {
      &::before {
        width: 100vw;
      }
    }
    &:first-child {
      ._dashed-line {
        display: none;
      }
    }
    &:last-child {
      &::before {
        right: 0;
        left: auto;
      }
      &::after {
        display: none;
      }
    }
  }
  &--alternate {
    .reason__image {
      .reason__image-wrapper {
        transform: scaleX(-1);
      }
      &::before {
        transform: rotate(90deg);
        left: auto;
        right: rem(21);
      }
      &::after {
        transform: rotate(90deg);
        right: rem(9);
        left: auto;
      }
    }
  }
  // .reason__image
  &__image {
    position: relative;
    z-index: 11;
    margin-bottom: rem(30);
    @include size(143, 124);
    @media (max-width: $phone + px) {
      @include size(106, 92);
    }
    @media (max-width: $smallPhone + px) {
      margin-bottom: rem(11);
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      background: center / contain no-repeat var(--gradient-dot);
    }
    &::before {
      width: rem(22);
      height: rem(22);
      top: rem(-4);
      left: rem(21);
      z-index: 20;
      @media (max-width: $phone + px) {
        @include size(16);
      }
    }
    &::after {
      @include size(4);
      top: rem(-14);
      left: rem(9);
      @media (max-width: $phone + px) {
        @include size(6);
        top: rem(-10);
        left: rem(9);
      }
    }
  }
  &__image-wrapper {
    position: relative;
    @include flex-center;
    width: 100%;
    height: 100%;
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
    background: linear-gradient(
      339.94deg,
      rgba(243, 248, 253, 1) 34.09%,
      rgba(244, 245, 252, 1) 61.35%,
      rgba(246, 241, 253, 1) 95.25%
    );
    // background: linear-gradient(
    //   339.94deg,
    //   rgba(78, 156, 219, 0.6) 34.09%,
    //   rgba(87, 97, 215, 0.6) 61.35%,
    //   rgba(118, 40, 218, 0.6) 95.25%
    // );
  }
  // .reason__text
  &__text {
    font-weight: 500;
    font-size: rem(17);
    line-height: math.div(26, 17);
    text-align: center;
    flex-grow: 1;
    @media (max-width: $phone + px) {
      font-size: rem(14);
    }
  }
}
