.whyus {
  background: center top / auto 100% no-repeat url(@img/whyus/whyus-bg.svg);
  color: $white;
  margin-top: rem(-210 - 200);
  padding-bottom: rem(138);
  overflow: hidden;
  position: relative;
  @media (max-width: $phone + px) {
    margin-top: rem(-20 - 100);
    background-image: url(@img/whyus/whyus-bg-phone.png);
    padding-bottom: rem(80);
    &::before {
      content: "";
      position: absolute;
      right: rem(-85);
      top: rem(60);
      @include size(108, 86);
      background: no-repeat var(--hexagon-filled-squished);
      @media (max-width: $smallPhone + px) {
        display: none;
      }
    }
  }
  &::before, &::after {
    content: '';
    position: absolute;
    @media (max-width: 1420px) {
      display: none;
    }
  }
  &::before {
    left: 0;
    top: 40%;
    @include size(99, 88);
    transform: translate(-35%, 0);
    background: no-repeat url('data:image/svg+xml;utf8,<svg viewBox="0 0 190 170" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.3" d="M139.102 10.9468L176.407 75.549L139.102 10.9468ZM139.102 10.9468C136.974 7.26317 133.051 5 128.804 5H54.1941C49.949 5 46.0262 7.26253 43.8952 10.9482L6.59318 75.5492C4.46937 79.2308 4.46883 83.7601 6.59224 87.4431C6.59261 87.4437 6.59297 87.4444 6.59334 87.445L43.8952 152.051C46.0263 155.737 49.9492 158 54.1941 158H128.804C133.05 158 136.973 155.737 139.101 152.054C139.101 152.054 139.101 152.053 139.102 152.053L176.407 87.4452C178.531 83.7625 178.531 79.2331 176.408 75.5511L139.102 10.9468Z" stroke="url(%23paint0_linear_82_22459)" stroke-width="20"/><defs><linearGradient id="paint0_linear_82_22459" x1="91.5" y1="0" x2="91.5" y2="163" gradientUnits="userSpaceOnUse"><stop stop-color="%236D54DB"/><stop offset="1" stop-color="%2359A2DD"/></linearGradient></defs></svg>');
  }
  &::after {
    right: 0;
    bottom: 20%;
    @include size(183, 163);
    transform: translate(60%, 0);
    background: no-repeat url('data:image/svg+xml;utf8,<svg width="183" height="163" viewBox="0 0 183 163" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.3" d="M139.102 10.9468L176.407 75.549L139.102 10.9468ZM139.102 10.9468C136.974 7.26317 133.051 5 128.804 5H54.1941C49.949 5 46.0262 7.26253 43.8952 10.9482L6.59318 75.5492C4.46937 79.2308 4.46883 83.7601 6.59224 87.4431C6.59261 87.4437 6.59297 87.4444 6.59334 87.445L43.8952 152.051C46.0263 155.737 49.9492 158 54.1941 158H128.804C133.05 158 136.973 155.737 139.101 152.054C139.101 152.054 139.101 152.053 139.102 152.053L176.407 87.4452C178.531 83.7625 178.531 79.2331 176.408 75.5511L139.102 10.9468Z" stroke="url(%23paint0_linear_82_22459)" stroke-width="10"/><defs><linearGradient id="paint0_linear_82_22459" x1="91.5" y1="0" x2="91.5" y2="163" gradientUnits="userSpaceOnUse"><stop stop-color="%236D54DB"/><stop offset="1" stop-color="%2359A2DD"/></linearGradient></defs></svg>');
  }
  @media (max-width: $smallPhone + px) {
    padding-bottom: rem(45-100);
    margin-top: rem(-160);
  }
  // .whyus__container
  &__container {
    padding-top: rem(304);
    position: relative;
    z-index: 5;
    @media (max-width: $phone + px) {
      padding-top: rem(120);
    }
    @media (max-width: $smallPhone + px) {
      padding-top: rem(130);
    }
  }
  &__bg-left {
    position: absolute;
    left: 0;
    bottom: 15%;
    width: 100%;
    height: 565px;
    max-width: 75%;
    @media (max-width: 768px) {
      display: none;
    }
  }
  &__bg-right {
    position: absolute;
    right: 0;
    bottom: 10%;
    width: 100%;
    height: 565px;
    max-height: 70%;
    @media (max-width: 768px) {
      bottom: 0;
      height: 565px;
    }
    @media (max-width: 500px) {
      height: 364px;
    }
  }
  // .whyus__header
  &__header {
    @include aminvalue("margin-bottom", 58, 17, $phone);
    @media (max-width: $phone + px) {
    }
  }
  // .whyus__title
  &__title {
    @media (max-width: $smallPhone + px) {
      text-align: center;
      margin-bottom: rem(19);
      padding-left: 0;
      font-size: rem(23);
      ._accent {
        font-size: rem(27);
      }
    }
    ._decor-hexagon._right {
      &::after {
        @media (max-width: 700px) {
          display: none;
        }
      }
    }
    ._decor-hexagon._left {
      &::before {
        display: none;
        @media (max-width: 700px) {
          display: inline-block;
        }
      }
    }
  }
  // .whyus__body
  &__body {
    display: flex;
    column-gap: rem(40);
    justify-content: space-between;
    @media (max-width: $phone - 68 + px) {
      flex-direction: column;
    }
  }
  // .whyus__content
  &__content {
  }
  // .whyus__content-title
  &__content-title {
    font-size: rem(30);
    line-height: math.div(37, 30);
    font-weight: rem(700);
    @include aminvalue("margin-bottom", 32, 15, $phone);
    @media (max-width: $phone + px) {
      font-size: rem(19);
    }
    @media (max-width: $phone - 68 + px) {
      margin-top: rem(25);
      margin-bottom: rem(21);
    }
    @media (max-width: $smallPhone + px) {
      margin-top: rem(15);
      margin-bottom: rem(10);
    }
  }
  // .whyus__text
  &__text {
    font-weight: 500;
    p:not(:last-child) {
      margin-bottom: rem(20);
      @media (max-width: $phone + px) {
        margin-bottom: rem(15);
      }
    }
    @media (max-width: $phone + px) {
      font-weight: 400;
      font-size: rem(14);
    }
  }
  // .whyus__graph
  &__graph {
    margin-top: rem(-120);
    flex: 0 0 pc(604, $maxWidthContainer);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @media (max-width: $phone + px) {
      flex-basis: rem(338);
      margin-top: 0;
    }
    @media (max-width: $phone - 68 + px) {
      flex-basis: auto;
      margin-top: rem(30);
    }
    @media (max-width: $smallPhone + px) {
      position: relative;
      margin-top: 0;
      &::before {
        content: "";
        position: absolute;
        left: rem(-85);
        bottom: 67%;
        @include size(108, 86);
        background: no-repeat var(--hexagon-filled-squished);
      }
    }
  }
  &__image {
    position: relative;  
    @media (max-width: 700px) {
      padding-top: 75%;
    }
    img {
      margin-top: rem(140);
      width: 100%;
      @media (max-width: $phone + px) {
        margin-top: rem(80);
      }
      @media (max-width: 700px) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
      }
    }
  }
  // .whyus__levels
  &__levels {
    display: flex;
    height: 49%; // 190px
    width: 100%;
    position: absolute;
    bottom: pc(150, 261);
    left: 0;
    justify-content: space-between;
    @media (max-width: $phone + px) {
      height: rem(120);
      margin-top: rem(0);
      justify-content: space-around;
    }
    @media (max-width: $phone - 68 + px) {
      height: rem(200);
    }
    @media (max-width: $smallPhone + px) {
      bottom: 55%;
      height: rem(90);
    }
  }
  // .whyus__level
  &__level {
    width: pc(170, 608);
    height: pc(70, 261);
    border-radius: rem(20);
    @include aminvalue("font-size", 19, 12, $phone);
    line-height: math.div(26, 19);
    text-align: center;
    @include flex-center;
    background: linear-gradient(
      270deg,
      rgba(#2e286f, 0.33) 6.58%,
      rgba(#34179b, 0.33) 91.84%
    );
    position: relative;
    @media (max-width: $phone + px) {
      @include size(88, 46);
      font-size: rem(12);
      border-radius: rem(10);
      padding-left: rem(20);
      padding-right: rem(20);
    }
    @media (max-width: $phone - 68 + px) {
      font-size: rem(17);
      width: pc(170, 608);
      height: pc(70, 261);
    }
    @media (max-width: $smallPhone + px) {
      font-size: rem(12);
      line-height: math.div(17, 12);
      width: pc(88, 280);
      height: pc(46, 158);
    }
    &::before {
      @include abs-center;
      font-size: rem(64);
      line-height: 1;
      text-align: center;
      font-weight: 600;
      opacity: 0.1;
      @media (max-width: $phone + px) {
        font-size: rem(47);
      }
      @media (max-width: $smallPhone + px) {
        font-size: rem(26);
      }
    }
    &:nth-child(1) {
      align-self: flex-end;
      &::before {
        content: "01";
      }
    }
    &:nth-child(2) {
      align-self: center;
      &::before {
        content: "02";
      }
    }
    &:nth-child(3) {
      align-self: flex-start;
      &::before {
        content: "03";
      }
    }
  }
}
