.first-screen {
  // min-height: rem(735 + 114);
  position: relative;
  overflow: hidden;
  background: #050219;
  // .first-screen__container
  &__container {
    display: flex;
    align-items: flex-start;
    padding: 0;
    position: relative;
    z-index: 3;
    @media (max-width: $phone  + px) {
      margin-top: rem(108);
    }
    @media (max-width: $phone - 1 + px) {
      flex-wrap: wrap;
      text-align: center;
    }
    @media (max-width: $smallPhone  + px) {
      margin-top: rem(42);
    }
    &::before {
      content: "";
      position: absolute;
      width: 431px;
      height: 435px;
      left: -235px;
      top: -218px;
      background: 
        linear-gradient(0deg, #043D98, #043D98),
        radial-gradient(349.25% 349.25% at 50.09% -106.72%, #000000 0%, rgba(255, 255, 255, 0) 100%);
      opacity: 0.54;
      filter: blur(107px);
    }
    &::after {
      content: '';
      position: absolute;
      width: 431px;
      height: 435px;
      right: 0;
      bottom: 0;
      background: #043D98;
      opacity: 0.45;
      z-index: 3;
      filter: blur(107px);
      transform: translate(70%, 30%);
    }
  }
  &__footer {
    position: relative;
    padding-bottom: pc(131, 1920);
    background-color: #050219;
    svg {
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 4px);
      pointer-events: none;
      z-index: 3;
      margin-top: -2px;
      height: calc(100% + 4px);
    }
  }
  // .first-screen__text-block
  &__text-block {
    margin-top: rem(114);
    flex: 0 0 pc(768, $maxWidthContainer);
    z-index: 1;
    @media (max-width: 800 + px) {
      flex-basis: pc(670, $maxWidthContainer);
    }
    @media (max-width: $phone + px) {
      margin-top: rem(30);
    }
    @media (max-width: $phone  - 1+ px) {
      flex: 0 0 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    @media (max-width: $smallPhone + px) {
      margin-top: rem(70);
    }
  }
  // .first-screen__video-block
  &__video-block {
    min-width: rem(297);
    margin-top: rem(114);
    position: absolute;
    right: -18%;
    top: 0;
    width: 546px;
    z-index: 0;
    @media (max-width: 1376 + px) {
      right: -14%;
    }
    @media (max-width: 1140 + px) {
      // right: -12%;
    }
    @media (max-width: $tablet + px) {
      display: flex;
    }
    @media (max-width: $phone + px) {
      margin-top: rem(30);
    }
    @media (max-width: $phone - 1 + px) {
      position: static;
      align-items: center;
      justify-content: center;
      margin-top: 0;
      flex-basis: 100%;
      width: 100%;
    }
  }
  // .first-screen__video
  &__video {
    width: 100%;
    @media (max-width: $tablet - 1 + px) {
      // margin-top: rem(30);
    }
    @media (max-width: $phone - 1 + px) {
      // width: pc(197, 320);
      width: 80%;
    }
  }
}
.text-block {
  color: #fff;
  padding-bottom: rem(27);
  @media (max-width: 1200 + px) {
    padding-left: rem(45);
  }
  @media (max-width: $phone - 1 + px) {
    padding-left: 0;
  }
  // .text-block__slogan
  &__slogan {
    font-weight: 500;
    font-size: rem(21);
    line-height: math.div(30, 21);
    margin-top: rem(117);
    margin-bottom: rem(11);
    position: relative;
    z-index: 5;
    @media (max-width: $tablet + px) {
      margin-top: rem(30);
    }
    @media (max-width: $phone + px) {
      margin-top: 0;
    }
    @media (max-width: $smallPhone + px) {
      font-size: rem(14);
      margin-bottom: rem(9);
    }
  }
  // .text-block__title
  &__title {
    font-weight: 700;
    font-size: rem(96);
    @include aminvalue("font-size", 96, 58, $phone);
    @include aminvalue("font-size", 58, 36, $smallPhone);
    line-height: em(142, 96);
    height: em(150, 96);
    margin-bottom: rem(10);
    position: relative;
    z-index: 1;
    ._decor-hexagon {
      &::before {
        width: 120px;
        @media (max-width: 1024px) {
          width: 90px;
        }
        @media (max-width: 480px) {
          width: 60px;
        }
      }
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      @include size(20);
      background: center / contain var(--gradient-dot);
      transform: translate(-250%, -110%);
      @media (max-width: 768px) {
        @include size(11);
      }
      @media (max-width: 480px) {
        @include size(7);
      }
    }
  }
  // .text-block__text
  &__text {
    font-weight: 300;
    font-size: rem(21);
    line-height: math.div(30, 21);
    margin-bottom: rem(42);
    @media (max-width: $phone + px) {
      font-size: rem(18);
    }
    @media (max-width: $phone - 1+ px) {
      padding: 0 10%;
      margin-bottom: 25px;
    }
    @media (max-width: 480px) {
      margin-bottom: 15px;
    }
  }
  // .text-block__buttons
  &__buttons {
    display: flex;
    gap: rem(20);
    margin-bottom: rem(78);
    align-items: flex-start;
    @media (max-width: $phone + px) {
    }
    @media (max-width: $phone - 1 + px) {
      margin-bottom: rem(45);
      margin-top: rem(-60);
      margin-bottom: rem(60);
      justify-content: space-evenly;
    }
    @media (max-width: 480 + px) {
      margin-bottom: rem(30);
      margin-top: rem(-40);
      flex-wrap: wrap;
    }
  }
  // .text-block__action
  &__action {
    flex: 0 1 rem(204);
    @media (max-width: 480 + px) {
      flex: 0 0 pc(204, 320);
      // width: 100%;
    }
  }
  // .text-block__about-us
  &__about-us {
  }
  // .text-block__know-more
  &__know-more {
  }
  // .text-block__services
  &__services {
    display: flex;
    flex-wrap: wrap;
    gap: rem(20) rem(48);
    @media (max-width: $tablet + px) {
      justify-content: space-evenly;
      gap: 0;
    }
    @media (max-width: $phone - 1 + px) {
      // justify-content: center;
    }
  }
  // .text-block__service
  &__service {
    opacity: 0.25;
    transition: opacity .3s;
    @media (any-hover: hover) {
      &:hover {
        opacity: 0.5;
      }
    }
    @media (max-width: $phone + px) {
      // width: rem(60);
      width: 26%;
    }
    img {
      width: 100%;
    }
  }
}
