.header {
  position: fixed;
  height: rem(114);
  line-height: math.div(26, 16);
  font-weight: 500;
  width: 100%;
  z-index: 50;
  transition: background-color .3s;
  &._scrolled {
    background-color: #060329;
    box-shadow: 1px 1px 20px rgba(0,0,0,.75);
  }
  @media (max-width: $tablet + px) {
    height: rem(94);
    background: linear-gradient(90deg, #043d98, #050116 70%);
  }
  @media (max-width: $smallPhone + px) {
    height: rem(80);
  }
  // .header__container
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: rem(44) rem(30);
    height: 100%;
  }
  &__logo {
    max-width: pc(266, $maxWidthContainer);
  }
  &__socials-list {
    display: none;
    @media (max-width: 768px) {
      display: flex;
      flex-wrap: wrap;
      margin-top: rem(84 - 21);
      margin-bottom: rem(40);
      margin-left: rem(38);
      gap: rem(20);
      @media (max-width: $phone + px) {
        margin: rem(20) rem(30)
      }
    }
  }
  &__social-link {
    @include size(45);
    backdrop-filter: blur(15px);
  }
  // .header__menu
  &__menu {
    color: rgba(#fff, 0.81);
    overflow: auto;
    @media (min-width: 769px) {
      display: block;
      margin-left: auto;
    }
    @media (max-width: 768px) {
      position: absolute;
      top: rem(94);
      left: 100%;
      height: calc(100vh - 100%);
      transition: transform .5s;
      overflow: auto;
      min-width: 400px;
    }
    @media (max-width: $phone + px) {
      font-size: rem(14);
    }
    @media (max-width: 400px) {
      min-width: auto;
      width: 100%;
    }
    @media (max-width: $smallPhone + px) {
      top: rem(80);
    }
    &._open {
      transform: translateX(-100%);
      z-index: 100;
    }
  }
  &__menu-body {
    @media (max-width: 768px) {
      background: #000;
      position: relative;
      z-index: 10;
      height: 100%;
      overflow: auto;
    }
  }
  &__menu-block {
  }
  // .header__menu-list
  &__menu-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: rem(30) rem(10);
    font-size: rem(18);
    line-height: math.div(26, 18);
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
      margin-top: rem(87 - 12);
      row-gap: rem(36 - 12 * 2);
      @media (max-width: $phone + px) {
        margin-top: rem(20);
      }
    }
  }
  // .header__menu-item
  &__menu-item {
    @include aminvalue("padding-left", 30, 10, $tablet);
    @include aminvalue("padding-right", 30, 10, $tablet);
    @media (max-width: 768px) {
      width: 100%;
      padding: 0;
    }
  }
  // .header__menu-link
  &__menu-link {
    transition: color 0.2s;
    @media (any-hover: hover) {
      &:hover {
        color: #fff;
      }
    }
    @media (max-width: 768px) {
      display: block;
      padding-left: rem(38);
      padding-top: rem(12);
      padding-bottom: rem(12);
      @media (max-width: $phone + px) {
        padding-left: rem(40);
        width: 100%;
      }
    }
  }
}

.icon-menu {
  display: none;
  @media (max-width: $phone + px) {
    display: block;
    width: rem(67);
    flex: 0 0 rem(67);
    height: rem(44);
    border-radius: 60px;
    border: 2px solid $mainBlue;
    cursor: pointer;
    z-index: 5;
    position: relative;
    @media (max-width: $smallPhone + px) {
      flex: 0 0 rem(45);
      width: rem(45);
      height: rem(30);
    }
    span,
    &::before,
    &::after {
      content: "";
      transition: transform 0.2s;
      position: absolute;
      top: 50%;
      left: 50%;
      width: rem(23);
      height: 2px;
      background-color: #fff;
      @media (max-width: $smallPhone + px) {
        width: rem(12);
      }
    }
    &::before {
      transform: translate(-50%, calc(-50% - 5px));
    }
    &::after {
      transform: translate(-50%, calc(-50% + 5px));
    }
    span {
      transform: translate(-50%, -50%);
      top: 50%;
    }
    &._open {
      span {
        transform: scale(0);
      }
      &::before {
        transform: translate(-50%, 0%) rotate(-45deg);
        top: 50%;
        left: 50%;
      }
      &::after {
        transform: translate(-50%, 0%) rotate(45deg);
        top: 50%;
        left: 50%;
      }
    }
  }
}
