.about {
  position: relative;
  margin-top: rem(93);
  overflow-x: clip;
  overflow-y: visible;
  @media (max-width: 1000px) {
    background: 100% 185% no-repeat url(@img/about/about-bg.png);
  }
  @media (max-width: $smallPhone + px) {
    margin-top: rem(28);
    background-position: 95% 115%;
  }
  &::before {
    content: "";
    position: absolute;
    bottom: 10%;
    transform: translateX(-70%);
    width: rem(108);
    height: rem(81);
    z-index: 10;
    background: no-repeat var(--hexagon-filled-squished);
    @media (min-width: #{$maxWidth + 1}px) {
      display: none;
    }
    @media (max-width: 1000px) {
      display: none;
    }
  }
  &__bg {
    display: flex;
    align-items: flex-start;
  }
  &__image {
    position: relative;
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    // outline: 5px solid rebeccapurple;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: -20%;
      height: 473px;
      width: 475px;
      transform: translate(0, -50%);
      background: center / contain no-repeat var(--hexagon-large-filled-squished);
    }
    @media (max-width: 1000px) {
      justify-content: center;
      height: 100%;
      &::before {
        top: 0;
        left: 50%;
        transform: translate(-100%, -5%);
        width: rem(213);
        height: rem(195);
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: rem(169);
      height: rem(151);
      bottom: 0;
      right: 0;
      z-index: 0;
      background: center/cover no-repeat
        var(--hexagon-empty-thin);
      @media (max-width: 1000px) {
        bottom: 0;
        left: 50%;
      }
    }
    img {
      &.loading {
        height: 100%;
      }
      position: relative;
      max-width: 100%;
      max-height: 100%;
      z-index: 4;
      @media (max-width: 1000px) {
        width: auto;
      }
    }
  }
  // .about__container
  &__container {
    display: flex;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: rem(56);
      left: rem(-120);
      width: rem(85);
      height: rem(76);
      background: no-repeat var(--hexagon-thick-empty);
    }
    @media (max-width: 1000px) {
      &::after {
        content: "";
        position: absolute;
        right: rem(-108 + 27);
        top: rem(-48);
        width: rem(108);
        height: rem(81);
        z-index: 10;
        background: no-repeat var(--hexagon-filled-squished);
      }
    }
    @media (max-width: $smallPhone + px) {
      &::after {
        right: rem(-108 + 18);
      }
    }
    @media (max-width: 1000px) {
      flex-direction: column;
      padding-left: rem(10);
      padding-right: rem(10);
    }
  }
  $contentWidth: 665;
  // .about__content
  &__content {
    // padding-left: rem(104);
    padding-left: pc(104, $containerWidth);
    flex: 0 0 pc($contentWidth, $maxWidthContainer);
    @media (max-width: 1000px) {
      flex: 0 0 100%;
      padding-left: 0;
      display: flex;
      flex-direction: column;
    }
  }
  // .about__phone
  &__phone {
    flex: auto;
    position: relative;
    @media (max-width: 1000px) {
      flex: 0 0 100%;
      min-height: rem(500);
      width: 100%;
      max-width: 500px;;
      // margin-top: rem(-40);
      align-self: center;
    }
  }
  &__messages-list {
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 10;
    height: 100%;
    padding: rem(30) 0;
    gap: rem(60);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    height: 100%;
    // outline: 4px solid greenyellow;
  }
  &__messages-list-item {
    // outline: 1px solid red;
    &:nth-child(odd) {
      margin-left: 80px;
      @media (max-width: 375px) {
        margin-left: 10%;
      }
    }
    &:nth-child(even) {
      margin-right: 80px;
      @media (max-width: 375px) {
        margin-right: 10%;
      }
    }
    @media (max-width: 1000px) {
      max-width: rem(420);
      min-width: rem(400);
    }
    @media (max-width: 480px) {
      min-width: 80%;
      max-width: 100%;
    }
    @media (max-width: 375px) {
      min-width: 90%;
    }
  }
  // .about__phone-message
  &__phone-message {
    width: 100%;
    // position: absolute;
    z-index: 10;
    right: 0;
    font-size: rem(16);
    padding: rem(20);
    @media (max-width: 1440px) {
      font-size: 14px;
    }
    @media (max-width: 1000px) {
      font-size: rem(12);
      line-height: math.div(18, 12);
    }
    @media (max-width: 480px) {
      padding: rem(15);
    }
  }

  // .about__title
  &__title {
    margin-bottom: rem(15);
    ._decor-hexagon._left {
      &::before {
        height: pc(91, 56);
        width: 106px;
        // transform: translate(-50%, -50%);
      }
    }
    @media (max-width: 1000px) {
      text-align: center;
      ._decor-hexagon._left {
        &::before {
          width: 63px;
          height: pc(55, 37);
          // transform: translate(-30%, -50%);
        }
      }
      @media (max-width: 480px) {
      ._decor-hexagon._left {
        &::before {
          width: 46px;
          height: pc(39, 37);
          // transform: translate(-30%, -50%);
        }
      }
      }
    }
  }
  // .about__sub-title
  &__sub-title {
    font-weight: 600;
    font-size: rem(28);
    line-height: math.div(37, 28);
    margin-bottom: rem(26);
    @media (max-width: 1000px) {
      font-size: rem(18);
      margin-bottom: rem(15);
    }
    @media (max-width: $smallPhone + px) {
      font-size: rem(16);
    }
  }
  // .about__text
  &__text {
    font-size: rem(16);
    line-height: math.div(28, 16);
    padding-bottom: rem(55);
    padding-right: rem(47);
    margin-right: rem(-47);
    position: relative;
    &::before,
    &::after {
      content: "";
      position: absolute;
    }
    &::before {
      right: 0;
      bottom: 0;
      border-right: 1px dashed $accentBlue;
      height: pc(168, 236);
      width: 1px;
    }
    &::after {
      bottom: pc(168, 236);
      right: 0;
      transform: translateX(100%);
      height: 1px;
      width: 100vw;
      border-top: 1px dashed $accentBlue;
    }
    p:not(:last-child) {
      margin-bottom: rem(10);
    }
    @media (max-width: 1000px) {
      font-size: rem(14);
      padding-bottom: rem(25);
    }
  }
  // .about__offers
  &__offers {
    padding-left: pc(104, $contentWidth - 104);
    margin-left: pc(-104, $contentWidth - 104);
    @media (max-width: 1000px) {
      padding-left: rem(15);
      margin-left: 0;
      &::before {
        content: "";
        position: absolute;
        right: rem(-108 - 10 + 27);
        top: 50%;
        transform: translateY(-50%);
        width: rem(108);
        height: rem(81);
        background: no-repeat var(--hexagon-filled-squished);
      }
    }
  }
}
.message {
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  color: $black;
  // .message__header
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: rem(5);
    height: rem(21);
    &:not(:last-child) {
      margin-bottom: rem(19);
      @include abetweenvalue(
        "margin-bottom",
        19,
        10,
        $maxWidthContainer,
        1000
      );
      @media (max-width: 1000px) {
        margin-bottom: rem(10);
      }
    }
  }
  // .message__messenger-logo
  &__messenger-logo {
    font-size: rem(21);
    background: linear-gradient(
        153.43deg,
        rgba(203, 203, 203, 0.2) -2.07%,
        rgba(0, 0, 0, 0) 200%
      ),
      #000000;
    border-radius: 4px;
    color: $white;
  }
  // .message__messenger-title
  &__messenger-title {
    flex-grow: 1;
    text-transform: uppercase;
    line-height: 1;
    font-size: rem(16);
    opacity: 0.4;
    @media (max-width: 1000px) {
      font-size: rem(14);
    }
  }
  // .message__time
  &__time {
    opacity: 0.4;
    @media (max-width: 1000px) {
      font-size: rem(10);
      align-self: flex-start;
    }
  }
  // .message__body
  &__body {
    line-height: math.div(20, 16);
    color: $black;
    @media (max-width: 1000px) {
      padding-right: rem(0);
    }
  }
}
.offers {
  padding-top: rem(28);
  padding-bottom: rem(37);
  position: relative;
  @media (max-width: 1000px) {
    padding-top: rem(27);
    padding-bottom: rem(30);
  }
  // .offers__title
  &__title {
    font-size: rem(26);
    line-height: math.div(42, 26);
    margin-bottom: rem(35);
    text-transform: uppercase;
    font-weight: 700;
    @media (max-width: 1000px) {
      font-size: rem(20);
      margin-bottom: rem(15);
      max-width: 80%;
    }
    @media (max-width: 480 + px) {
      font-size: rem(16);
      max-width: 100%;
    }
  }
  // .offers__list
  &__list {
    font-size: rem(18);
    line-height: math.div(24, 18);
    display: grid;
    grid-template: auto / repeat(2, 1fr);
    gap: rem(20) rem(10);
    justify-content: space-between;
    @media (max-width: 1000px) {
      gap: rem(16) rem(10);
      grid-template: auto / 40% 60%;
    }
    @media (max-width: 600 + px) {
      gap: rem(16) rem(10);
      grid-template: auto / auto;
    }
  }
  // .offers__offer
  &__offer {
  }
}

.offer {
  display: flex;
  align-items: center;
  @media (max-width: 1000px) {
    font-size: rem(14);
    line-height: math.div(20, 14);
  }
  &__image {
    display: block;
    width: rem(57);
    height: rem(57);
    flex: 0 0 rem(57);
    @include flex-center;
    img {
      margin-bottom: rem(-8);
    }
    @media (max-width: 1000px) {
      width: rem(52);
      height: rem(52);
      flex: 0 0 rem(52);
    }
  }
  &__text {
    margin-left: rem(20);
    @media (max-width: 1000px) {
      margin-left: rem(13);
    }
  }
}
.about ._dashed-line {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: calc(100% + rem(47));
  border-top: 1px dashed #6f7ade;
  border-left: 1px dashed #6f7ade;
  z-index: -10;

  &::before {
    content: "";
    width: 100vw;
    height: 1px;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translateX(-100%);
    border-top: 1px dashed #6f7ade;
  }
  @media (max-width: 1000px) {
    height: pc(194, 320);
    height: 100%;
    &::before {
      transform: none;
    }
  }
}