.signals {
  color: $white;
  @include avalue("padding-top", 150, 10);
  @include avalue("padding-bottom", 100, 10);
  margin-top: -30px;
  position: relative;
  overflow-x: clip;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" fill="none" preserveAspectRatio="none" viewBox="0 46 1920 1041"><path d="M-1629.14 37.82c293.24 55.546 551.28 38.654 551.28 38.654s348.746-35.98 440.337-47.438C-545.931 17.58-703.147 47.94-501 69.917c202.148 21.979 807.581-37.535 1044-17.5 206.5 17.5 362.998 46.29 673.5 46.93 246.62.508 366.23 8.717 688.5-46.93 265.97-45.926 385.09-107.063 505.78 46.93 120.69 153.992 839 572.68 739.49 620.138-546.11 260.453-1422.48 346.445-1976.95 340.455-195.729-1.25-800.089-90.687-1164.727 25.25l-131.702 20.87-1626.521-467.201 6.66-602.297 113.83 1.258Z" fill="url(%23a)"/><defs><linearGradient id="a" x1="357.803" x2="155.596" y1="-45.631" y2="1532.29" gradientUnits="userSpaceOnUse"><stop stop-color="%23040114"/><stop offset=".124" stop-color="%23080435"/><stop offset=".454" stop-color="%23130F3D"/><stop offset="1"/></linearGradient></defs></svg>');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  @media (max-width: 1200px) {
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" fill="none" preserveAspectRatio="none" viewBox="0 70 768 860"><path d="M1711.89 970.431s254.11.417 538.56 70.369l111.59 8.28 41.51-559.436L1001.58 22.272s-563.097 139.976-859.785 92.416C-462.51 17.815-970.929-124.751-1422.24 213.822c-82.6 61.965-396.36 366.757-286.9 517.435 109.45 150.678 363.97 136.363 627.56 110.288 319.38-31.594 345.891-57.515 587.799-41.638 304.566 19.991 239.708 63.556 465.042 102.917 226.728 61.207 625.792-19.218 822.823 13.838 197.031 33.057 392.556 75.288 483.076 70.359 90.52-4.929 434.73-16.59 434.73-16.59Z" fill="url(%23a)"/><defs><linearGradient id="a" x1="296.408" x2="568.243" y1="994.253" y2="-463.219" gradientUnits="userSpaceOnUse"><stop stop-color="%23040114"/><stop offset=".124" stop-color="%23080435"/><stop offset=".454" stop-color="%23130F3D"/><stop offset="1" stop-color="%23130F3D" stop-opacity=".73"/></linearGradient></defs></svg>');
  }
  @media (max-width: 767px) {
    margin-top: 0;
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" fill="none" preserveAspectRatio="none" viewBox="0 100 320 1506"><path d="M1416.8 912.013s238.73.392 505.95 66.104l104.84 7.779 38.99-525.558L749.5 21.267s-529 131.5-807.724 86.819c-567.713-91.007-1045.346-224.94-1469.336 93.132-77.59 58.212-372.35 344.549-269.53 486.103 102.83 141.554 341.94 128.106 589.57 103.61 300.04-29.681 324.946-54.033 552.206-39.116C-369.19 770.594-254.19 1545.52-42.499 1582.5 170.5 1640 278.9 1567.94 464 1599c185.101 31.05 459.36-666.771 544.4-671.402 85.03-4.63 408.4-15.585 408.4-15.585Z" fill="url(%23a)"/><defs><linearGradient id="a" x1="87.027" x2="342.401" y1="934.393" y2="-434.827" gradientUnits="userSpaceOnUse"><stop stop-color="%23040114"/><stop offset=".124" stop-color="%23080435"/><stop offset=".454" stop-color="%23130F3D"/><stop offset="1" stop-color="%23130F3D" stop-opacity=".73"/></linearGradient></defs></svg>');
  }
  @media (max-width: 480px) {
    margin-top: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &::before {
    content: "";
    display: none;
    @include abs;
    width: 12%;
    height: 137px;
    transform: translate(pc(-105, 184), -50%);
    background: center / contain no-repeat
      var(--hexagon-empty-thin);
    @media (max-width: $phone + 100 + px) {
      transform: translate(-75%, -58%);
      background: center / contain no-repeat var(--hexagon-filled);
    }
    @media (min-width: $phone + px) {
      display: block;
    }
  }
  &__bg-left {
    position: absolute;
    left: 0;
    bottom: 15%;
    width: 100%;
    height: 565px;
    max-width: 75%;
    opacity: 0.5;
    @media (max-width: 768px) {
      display: none;
    }
  }
  &__bg-right {
    position: absolute;
    right: 0;
    bottom: 30%;
    width: 100%;
    height: 565px;
    max-height: 70%;
    opacity: 0.5;
    @media (max-width: 768px) {
      display: none;
    }
  }
  ._dashed-line {
    @include aminvalue("width", 234, 59, $phone);
    @include aminvalue("height", 77, 24, $phone);
    border-top: 1px dashed;
    position: absolute;
    bottom: -100%;
    color: $white;
    @media (max-width: 1300 + px) {
      color: #6f7ade;
      transform: scaleY(-1);
      bottom: 30%;
    }
    @media (max-width: 1024px) {
      bottom: -25%;
    }
    @media (max-width: 768px) {
      bottom: -15%;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      border-bottom: 1px dashed;
      height: 1px;
      width: 400%;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      @include size(11);
      background: center / contain var(--gradient-dot);
    }
    &-left {
      left: -25%;
      border-left: 1px dashed;
      &::before {
        right: 0;
        transform: translateX(calc(-100% / 400 * 100));
      }
      &::after {
        right: 0;
        transform: translate(100%, -50%) rotate(-90deg);
      }
      @media (max-width: 1300 + px) {
        left: -12%;
      }
      @media (max-width: 1024px) {
        left: 0%;
      }
    }
    &-right {
      right: -25%;
      border-right: 1px dashed;
      &::before {
        left: 0;
        transform: translateX(calc(100% / 400 * 100));
      }
      &::after {
        left: 0;
        transform: translate(-100%, -50%) rotate(-180 - 90deg);
      }
      @media (max-width: 1300 + px) {
        right: -12%;
      }
      @media (max-width: 1024px) {
        right: -0%;
      }
      @media (min-width: 1440px) {
        transform: scaleY(-1) translateY(100%);
      }
    }
  }
  // .signals__container
  &__container {
    position: relative;
    z-index: 5;
  }
  // .signals__header
  &__header {
    display: flex;
    column-gap: 50px;
    justify-content: space-between;
    @include avalue("margin-bottom", 50, 15);
    @media (max-width: 700 + px) {
      flex-direction: column;
      text-align: center;
      align-items: center;
      row-gap: rem(17);
    }
  }
  // .signals__title
  &__title {
    height: auto;
    ._accent {
      background: linear-gradient(#6d54db, #59a2dd);
      color: transparent;
      background-clip: text;
      -webkit-background-clip: text;
      @supports (-webkit-overflow-scrolling: touch) {
        -webkit-text-fill-color: unset;
      }
    }
    ._decor-hexagon._right {
      &::after {
        @media (max-width: 700px) {
          display: none;
        }
      }
    }
    ._decor-hexagon._left {
      &::before {
        display: none;
        @media (max-width: 700px) {
          display: inline-block;
        }
      }
    }
    ._responsive {
      @media (max-width: 700px) {
        display: block;
      }
    }
    @media (max-width: 700 + px) {
      text-align: center;
    }
  }
  // .signals__sub-title
  &__sub-title {
    flex: auto;
    ._accent {
      font-size: rem(18);
      line-height: math.div(28, 18);
      font-weight: 700;
      color: $purple;
    }
    @media (max-width: 700 + px) {
      width: 85%;
    }
  }
  // .signals__body
  &__body {
  }
  &__data {
    display: flex;
    gap: 20px 32px;
    @media (max-width: $phone + 100 + px) {
      column-gap: rem(20);
    }
    @media (max-width: $phone - 1 + px) {
      flex-direction: column;
    }
  }
  // .signals__text
  &__text {
    font-size: rem(21);
    line-height: math.div(29, 21);
    font-weight: 400;
    margin-bottom: rem(40);
    position: relative;
    @media (max-width: 1300 + px) {
      text-align: center;
      padding: 0 rem(20);
    }
    @media (max-width: 600 + px) {
      font-size: rem(18);
      line-height: math.div(26, 18);
      padding: 0 rem(10);
    }
    ._accent {
      position: relative;
      font-weight: 700;
      font-size: rem(26);
      line-height: math.div(29, 26);
      text-transform: uppercase;
      color: $purple;
      @media (max-width: 600 + px) {
        font-size: inherit;
        line-height: math.div(26, 18);
        margin-top: rem(5);
        line-height: 1.4;
      }
      @media (max-width: 768px) {
        display: inline-block;
        margin-top: 20px;
        ._dashed-line-left {
            transform: scaleY(1);  
            top: rem(-10);
            left: -5%;
        }
        ._dashed-line-right {
          bottom: rem(-10);
          right: -5%;
        }
      }
    }
    ._responsive {
      &._before-accent {
        @media (max-width: 1024px) {
          display: block;
        }
      }
      @media (max-width: 768px) {
        display: block;
      }
    }
  }
  // .signals__reports
  &__reports {
    flex: 0 1 pc(269, $maxWidthContainer);
    padding: rem(30);
    background: linear-gradient(39.17deg, #231c64 0.07%, #231f50 80.92%);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    row-gap: rem(24);
    @media (max-width: 1000px) {
      padding: rem(24) rem(15);
    }
  }
  // .signals__report-list
  &__report-list {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    row-gap: rem(21);
    @media (max-width: 1000px) {
      flex-wrap: wrap;
      flex-direction: row;
      row-gap: rem(15);
    }
  }
  // .signals__report-item
  &__report-item {
    display: flex;
    column-gap: rem(20);
    align-items: center;
    @media (max-width: 1000px) {
      column-gap: rem(10);
    }
    @media (max-width: $phone - 1 + px) {
      flex: 0 0 50%;
      justify-content: center;
      flex-direction: column;
      row-gap: rem(10);
      text-align: center;
      &:nth-child(odd) {
        background: 100% 0 repeat-y
          url('data:image/svg+xml;utf8,<svg width="2" height="266" viewBox="0 0 2 266" fill="none" xmlns="http://www.w3.org/2000/svg"><path opacity="0.3" d="M1.00001 266L1 -0.000488281" stroke="%236F7ADE" stroke-width="0.8621" stroke-miterlimit="10" stroke-dasharray="6 6"/></svg>');
      }
    }
  }
  // .signals__report-item-image
  &__report-item-image {
    height: rem(43);
    width: rem(52);
    background: linear-gradient(
      359.47deg,
      rgba(#4e9cdb, 0.22) 52.27%,
      rgba(#818aef, 0.22) 127.32%,
      rgba(#7628da, 0.22) 161.09%
    );
    clip-path: polygon(25% 0%, 75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%);
    @include flex-center;
    @media (max-width: 1000px) {
      width: rem(45);
      height: rem(37);
    }
  }
  // .signals__report-item-text
  &__report-item-text {
    display: flex;
    flex-direction: column;
    column-gap: rem(20);
    @media (max-width: 768px) {
      column-gap: rem(10);
    }
  }
  // .signals__report-item-count
  &__report-item-value {
    font-weight: 500;
    font-size: rem(16);
    line-height: 1.5;
    // margin-top: rem(10);
    white-space: nowrap;
    @media (max-width: 1000px) {
      margin-top: 0;
    }
  }
  // .signals__report-item-label
  &__report-item-label {
    font-size: rem(13);
    line-height: math.div(15, 13);
    font-weight: 300;
    opacity: 0.88 * 0.74;
    @media (max-width: 768px) {
      font-size: rem(11);
    }
  }
  // .signals__report-button
  &__report-button {
    background: linear-gradient(
      122.93deg,
      #7345d3 -137.34%,
      #5470f0 38.32%,
      #1858cd 129.02%
    );
    box-shadow: 0px 9px 19px rgba(0, 0, 0, 0.15);
    border-radius: 60px;
    width: 100%;
    font-weight: 600;
    @media (max-width: 768px) {
      margin-left: auto;
      margin-right: auto;
    }
  }
  // .signals__chart
  &__chart {
    width: pc(849 - 30, $maxWidthContainer);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media (max-width: $phone - 1 + px) {
      width: 100%;
    }
  }
}
.chart {
  padding: rem(33) rem(29) rem(35) rem(23);
  background: linear-gradient(39.17deg, #231c64 0.07%, #231f50 80.92%);
  border-radius: 15px;
  &__chart-container {
    position: relative;
    background: 10px 0px /100% calc(100% - 30px) no-repeat;
  }
  // .chart__buttons
  &__buttons {
    margin-bottom: rem(18);
    display: flex;
    column-gap: rem(21);
    @media (max-width: $phone + px) {
      font-size: rem(14);
      gap: rem(14);
    }
    @media (max-width: 480 + px) {
      flex-wrap: wrap;
    }
  }
  &__button {
    background: linear-gradient(
      270deg,
      rgba(#403981, 0.34) 6.58%,
      rgba(#45318c, 0.34) 91.84%
    );
    border-radius: 8px;
    flex: 1;
    padding: rem(10) rem(24);
    @include flex-center;
    column-gap: rem(20);
    text-align: center;
    cursor: pointer;
    border: 1px solid transparent;
    @media (any-hover: hover) {
      &:hover {
        border-color: rgba(#4068e5, 0.5);
        background: linear-gradient(
          270deg,
          rgba(#403981, 0.68) 6.58%,
          rgba(#45318c, 0.68) 91.84%
        );
      }
    }
    @media (max-width: 1200 + px) {
      flex-direction: column;
      line-height: 1.3;
      padding: rem(15);
      row-gap: rem(15);
    }
    &--active {
      border-color: #4068e5;
      background: transparent;
      box-sizing: border-box;
      filter: drop-shadow(0px 9px 19px rgba(0, 0, 0, 0.15));
      opacity: 1;
    }
  }
  // .chart__button-text
  &__button-text {
  }
}
