.reviews {
  position: relative;
  overflow-x: clip;
  @include avalue('padding-bottom', 80, 40);
  @include avalue('margin-top', 80, 40);
  @media (min-width: 700px) {
    &::before {
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      @include aminvalue("width", 276, 139, $phone);
      @include aminvalue("height", 205, 104, $phone);
      transform: translateX(pc(276 - 65, 276));
      background: center / contain no-repeat var(--hexagon-filled);
    }
  }
  @media (min-width: 1440px) {
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 15%;
      @include size(178, 168);
      transform: translateX(-178px + 38);
      z-index: -1;
      background: center / contain no-repeat var(--hexagon-filled);
    }
  }
  // .reviews__container
  &__container {
    width: rem(1634);
    max-width: 100%;
    padding: 0;
    @media (max-width: 480px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  // .reviews__title
  &__title {
    text-align: center;
    @include avalue("margin-bottom", 67, 37);
    @media (max-width: 480px) {
      margin-left: auto;
      margin-right: auto;
    }
    @media (max-width: 480px) {
      ._decor-hexagon._left {
        &::before {
          display: none;
        }
      }
    }
    @media (min-width: 480px) {
      ._decor-hexagon._right {
        &::after {
          display: none;
        }
      }
    }
  }
  // .reviews__slider
  &__slider {
    width: 560px;
    max-width: 96%;
    margin: 0 auto;
    position: relative;
    perspective: 1200px;
    @media (max-width: 768px) {
      // width: 100%;
    }
    .swiper-button-prev,
    .swiper-button-next {
      top: 35%;
      z-index: 100;
      @include size(50);
      @media (max-width: 700px) {
        display: none;
      }
    }
    .swiper-button-prev {
      transform: translate(-150%, -50%);
      @media (max-width: 760px) {
        transform: translate(-100%, -50%);
      }
    }
    .swiper-button-next {
      transform: translate(150%, -50%);
      @media (max-width: 760px) {
        transform: translate(100%, -50%);
      }
    }
  }
}

.review {
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  transform-style: preserve-3d;
  @media (max-width: 768px) {
    flex: 0 0 100%;
  }
  &.swiper-slide {
    opacity: 0 !important;
    transition: transform .6s, opacity .6s;
    flex: 0 0 100%;
    @media (max-width: 768px) {
      opacity: 1 !important;
    }
    &-prev, &-next {
      opacity: .4 !important;
      @media (max-width: 768px) {
        opacity: 0 !important;
      }
    }
    &-active {
      opacity: 1 !important;
    }
    & .review__body {
      position: relative;
      ._gradient {
        background: linear-gradient(
          12.81deg,
          #4e9cdb 8.9%,
          #5761d7 88.4%,
          #7628da 108.06%
        );
        border-radius: 15px;
        opacity: 0;
        transition: opacity .6s;
        position: absolute;
        inset: 0 0 0 0;
        @media (max-width: 768px) {
          display: none;
        }
      }
    }
  }
  &.swiper-slide-active {
    & .review__body {
      ._gradient {
        opacity: 1;
      }
    }
  }
  &.swiper-slide-active {
    & .review__body {
      color: $white;
      box-shadow: 0px 4px 19px rgba(67, 132, 209, 0.50);
      @media (max-width: 768px) {
        color: $mainTextColor;
        box-shadow: 0px 4px 19px rgba($mainTextColor, 0.25);
      }
    }
  }
  // .review__body
  &__body {
    padding: rem(64) rem(34) rem(64 + 100) rem(34);
    font-weight: 400;
    transition: color .6s;
    background-color: #fff;
    box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    position: relative;
    flex-grow: 1;
    padding-bottom: rem(100);
    @media (max-width: 479px) {
      flex-grow: 0;
    }
    @media (max-width: 375px) {
      padding: rem(32) rem(17) rem(32 + 50) rem(17);
    }
    @media (min-width: $phone + px) {
      &::before {
        content: "";
        @include size(94, 81);
        background: center / cover var(--hexagon-empty-thin);
        position: absolute;
        right: 0;
        bottom: 0;
        transform: translate(50%, 50%);
        z-index: -1;
        opacity: 0.27;
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      @include size(66, 58);
      // blue quotes at the top of the block
      background: url('data:image/svg+xml;utf8,<svg width="66" height="58" viewBox="0 0 66 58" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M65.986 16.4472C65.986 7.76997 58.9521 0.736127 50.275 0.736126C41.5978 0.736126 34.564 7.76997 34.564 16.4472C34.564 25.1244 41.5978 32.1581 50.275 32.1581V32.1581C52.6063 32.1581 54.8399 34.1103 54.2078 36.3542C49.3363 53.6475 37.2739 63.7297 50.6733 53.8915C66.9273 41.9571 66.0034 16.9273 65.9852 16.4686C65.9852 16.4615 65.986 16.4551 65.986 16.4472Z" fill="url(%23paint0_linear_170_6372)"/><path d="M31.4215 16.4472C31.4215 7.76997 24.3876 0.736122 15.7104 0.736121C7.03323 0.73612 -0.000619333 7.76996 -0.000620092 16.4472C-0.00062085 25.1244 7.03336 32.1581 15.7106 32.1581V32.1581C18.0418 32.1581 20.2754 34.1103 19.6433 36.3542C14.7718 53.6475 2.70941 63.7297 16.1089 53.8915C32.3628 41.9571 31.439 16.9273 31.4207 16.4686C31.4207 16.4615 31.4215 16.4551 31.4215 16.4472Z" fill="url(%23paint1_linear_170_6372)"/><defs><linearGradient id="paint0_linear_170_6372" x1="69.5442" y1="247.053" x2="-55.6977" y2="202.336" gradientUnits="userSpaceOnUse"><stop stop-color="%237345D3"/><stop offset="0.644291" stop-color="%235470F0"/><stop offset="0.976972" stop-color="%231858CD"/></linearGradient><linearGradient id="paint1_linear_170_6372" x1="34.9798" y1="247.054" x2="-90.2626" y2="202.335" gradientUnits="userSpaceOnUse"><stop stop-color="%237345D3"/><stop offset="0.644291" stop-color="%235470F0"/><stop offset="0.976972" stop-color="%231858CD"/></linearGradient></defs></svg>');
    }
  }
  // .review__text
  &__text {
    position: relative;
    @include flex-center;
    flex-direction: column;
    height: 100%;
    @media (max-width: 479px) {
      height: auto;
    }
    p {
      &:not(:last-child) {
        margin-bottom: rem(15);
      }
    }
    &::before {
      content: "";
      @include abs-center;
      // white quotes
      background: center / contain no-repeat
        url('data:image/svg+xml;utf8,<svg width="234" height="203" viewBox="0 0 234 203" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M233.95 55.8702C233.95 25.1056 209.012 0.167402 178.248 0.1674C147.483 0.167397 122.545 25.1056 122.545 55.8702C122.545 86.6349 147.483 111.573 178.248 111.573V111.573C186.513 111.573 194.432 118.494 192.191 126.45C174.92 187.762 132.153 223.508 179.66 188.627C237.288 146.314 234.012 57.5726 233.947 55.9462C233.947 55.9209 233.95 55.8983 233.95 55.8702Z" fill="white"/><path d="M111.405 55.8702C111.405 25.1056 86.467 0.167389 55.7024 0.167386C24.9378 0.167384 -0.000405232 25.1056 -0.000407922 55.8702C-0.000410611 86.6348 24.9382 111.573 55.7029 111.573V111.573C63.9682 111.573 71.8873 118.494 69.6462 126.45C52.3747 187.762 9.60787 223.508 57.115 188.627C114.743 146.314 111.467 57.5726 111.402 55.9462C111.402 55.9209 111.405 55.8983 111.405 55.8702Z" fill="white"/></svg>');
      @include size(234, 202);
      max-height: 120%;
      opacity: 0.08;
      filter: drop-shadow(0px 9px 19px rgba(0, 0, 0, 0.15));
      color: $white;
    }
  }
  // .review__author
  &__author {
    text-align: center;
    margin-top: rem(-60);
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
  }
  // .review__author-image
  &__author-image {
    background: linear-gradient(
      12.81deg,
      #4e9cdb 8.9%,
      #5761d7 88.4%,
      #7628da 108.06%
    );
    box-shadow: 0px 4px 7px rgba(67, 132, 209, 0.31);
    border-radius: 13px;
    width: 118px;
    height: 118px;
    @include flex-center;
    img {
      border-radius: 13px;
      @include size(108);
    }
    &:not(:last-child) {
      margin-bottom: rem(10);
    }
  }
  // .review__author-name
  &__author-name {
    font-weight: 700;
    font-size: rem(24);
    line-height: math.div(42, 24);
  }
  // .review__author-description
  &__author-description {
    font-size: rem(17);
    line-height: 1.3;
  }
}
