@use "sass:math";
@import "common/mixins";
@import "common/reset";

$fontFamily: MazzardM;
$fontSize: 16;
$minFontSize: 14;
$lineHeight: 28;
$background: #fff;
$mainTextColor: #0d2a6b;
$mainBlue: #456ae7;
$accentBlue: #616bd9;
$lightBlue: #3990f9;
$black: #000;
$white: #fff;
$purple: #864dff;
$darkBlue: #456ae7;
$veryDarkBlue: #124796;
$sliderPaginationColor: #1d76fc;
$sliderLightButtonColor: #b6d3fe;

$minWidth: 320;
$maxWidth: 1920;
$maxWidthContainer: 1140;
$containerPadding: 30;
$containerWidth: $maxWidthContainer + $containerPadding;
// 1 no breakpoins
// 2 with breakpoints
$responsiveType: 1;
$pc: $maxWidthContainer + 12;
$tablet: 992; // tablets, phones horizontally
$phone: 768; // L phones
$smallPhone: 480; // S phones

html {
  scroll-behavior: smooth;
}
body {
  font-family: $fontFamily;
  @include avalue("font-size", $fontSize, $minFontSize);
  line-height: math.div($lineHeight, $fontSize);
  font-weight: 500;
  color: $mainTextColor;
  background-color: $background;
  position: relative;
  &::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    inset: 0 0 0 0;
    background: linear-gradient(180deg, #040114 0%, #07032b 100%);
    opacity: 0;
    transition: opacity .5s;
    z-index: 20;
  }
  &._lock {
    overflow: hidden;
    &::before {
      opacity: 0.75;
    }
  }
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &._loaded {
  }
  > main {
    // footer to the floor
    flex: 1 1 auto;
  }
  // fixing sliders
  > * {
    min-width: 0;
  }
}
@if ($responsiveType == 1) {
  [class*="__container"]/*,
    [class*="-container"]*/ {
    @if ($maxWidthContainer > 0) {
      max-width: $maxWidthContainer + px;
      box-sizing: content-box;
      margin: 0 auto;
    }
    @if ($containerPadding > 0) {
      padding: 0 math.div($containerPadding, 2) + px;
      @media (max-width: $smallPhone + px) {
        padding-left: rem(10);
        padding-right: rem(10);
      }
    }
  }
} @else if ($responsiveType == 2) {
  [class*="__container"],
  [class*="-container"] {
    margin: 0 auto;
    @if ($maxWidthContainer > 0) {
      max-width: $maxWidthContainer;
      box-sizing: content-box;
    } @else {
      @if ($containerPadding > 0) {
        padding: 0 math.div($container-padding, 2) + px;
      }
    }
    @media (max-width: $pc + px) {
      max-width: 970px;
    }
    @media (max-width: $tablet + px) {
      max-width: 750px;
    }
    @media (max-width: $phone + px) {
      max-width: none;
      @if ($containerPadding > 0 and $maxWidthContainer > 0) {
        padding: 0 math.div($containerPadding, 2) + px;
      }
    }
  }
} @else {
  @error "Unknown responsiveType: '" + $responsiveType + "'";
}

@import "common/fonts.scss";
@import "common/icons-base.scss";
@import "common/icons.scss";
@import "common/ui.scss";
@import "includes/about.scss";
@import "includes/first-screen.scss";
@import "includes/footer.scss";
@import "includes/header.scss";
@import "includes/learning.scss";
@import "includes/plans.scss";
@import "includes/reviews.scss";
@import "includes/signals.scss";
@import "includes/signup.scss";
@import "includes/whyus.scss";

