.signup {
  background: center top no-repeat;
  position: relative;
  overflow-x: clip;
  //   margin-top: rem(-128);
  //   @include aminvalue("margin-top", -128, -70, $phone);
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    @include size(468, 348);
    z-index: 0;
    background: center / cover var(--hexagon-filled-squished);
  }
  @media (min-width: 1200px) and (max-width: 1438px) {
    &::before {
      content: "";
      transform: translate(-85%, -78%);
    }
  }
  @media (min-width: 1440px){
    &::before {
      content: "";
      transform: translate(-85%, -85%);
    }
  }
  // .signup__container
  &__container {
    position: relative;
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      @include size(468, 348);
      z-index: 0;
      background: center / cover var(--hexagon-filled-squished);
    }
    @media (min-width: $phone + 1 + px) and (max-width: 1199px) {
      &::before {
        content: "";
        transform: translate(-85%, -70%);
      }
    }
    &::after {
      content: "";
      position: absolute;
      left: rem(-140);
      bottom: rem(200);
      @include size(47, 42);
      background: center / cover var(--hexagon-thick-empty);
    }
  }
  &__bg {
    position: absolute;
    left: 50%;
    top: 0;
    width: 100%;
    height: 503px;
    transform: translate(-50%, -40%);
    max-width: 1720px;
    object-fit: cover;
    z-index: -1;  
    @media (max-width: 768px) {
      transform: translate(-50%, -20%);      
    }
  }
  &__body {
    @include aminvalue("margin-top", 174, 79, $phone);
    @include aminvalue("margin-top", 79, 38, $smallPhone);
    position: relative;
    ._decor-hexagon-empty-thick-rounded {
      position: absolute;
      @include aminvalue("width", 178, 150, $phone);
      @include aminvalue("width", 159, 104, $smallPhone);
      @include aminvalue("height", 153, 130, $phone);
      @include aminvalue("height", 130, 90, $smallPhone);
      left: 160px;
      @include aminvalue("left", 160, 109, $phone);
      @include aminvalue("top", -50, -39, $phone);
      z-index: -1;
      background: center / contain no-repeat var(--hexagon-empty-thick-rounded);
      @media (max-width: 480 + px) {
        top: 60px;
        left: 60px;
      }
    }
    ._decor-signup-filled-hexagon {
      position: absolute;
      @include size(330, 294);
      @include aminvalue("height", 186, 146, $smallPhone);
      @include aminvalue("width", 330, 186, $phone);
      @include aminvalue("height", 146, 129, $smallPhone);
      @include aminvalue("width", 294, 164, $phone);
      background: center / contain no-repeat var(--hexagon-large-filled-squished);
      top: 50%;
      left: 50%;
      transform: rotate(180deg) translate(-45%, 65%) !important;
      z-index: -1;
      @media (max-width: 480 + px) {
        right: 0;
        left: auto;
        bottom: 120px;
        top: auto;
        transform: none;
      }
    }
  }
  &__image {
    margin: 0 auto;
    display: block;
    width: pc(853, $maxWidthContainer);
    @media (max-width: 880 + px) {
      width: pc(502, $phone - 15 * 2);
    }
    @media (max-width: 480px) {
      width: 90%;
    }
    @media (max-width: 375px) {
      width: 100%;
    }
  }

  // .signup__perks
  &__perks {
    position: absolute;
    top: 0;
    // @include aminvalue("top", 274, 159, $phone);
    // @include avalue('top')
    width: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 700 + px) {
      position: static;
      flex-direction: column;
      transform: none;
      margin-bottom: rem(15);
    }
  }
  // .signup__perk
  &__perk {
    &:nth-child(1) {
      @include aminvalue("margin-top", 107, 75, $phone);
      margin-left: rem(-30);
      @media (max-width: 1240 + px) and (min-width: 701px) {
        margin-left: 0;
      }
      @media (max-width: 700 + px) {
        margin-top: 0;
        margin-bottom: rem(24);
      }
    }
    &:nth-child(2) {
      @include aminvalue("margin-top", -50, -39, $phone);
      .perk__icon {
        width: rem(53);
      }
      .perk__icon-shadow {
        width: rem(53);
      }
    }
  }
  // .signup__offer
  &__offer {
    font-size: rem(25);
    line-height: math.div(37, 25);
    font-weight: 500;
    text-align: center;
    margin-bottom: rem(32);
    ._accent {
      text-transform: uppercase;
      color: #864DFF;
      font-weight: 700;
    }
    @media (max-width: 800px) {
      ._responsive._last {
        display: block;
      }
    }
    @media (max-width: 720 + px) {
      ._responsive {
        display: block;
      }
    }
    @media (max-width: 768px) {
      ._decor-hexagon._right {
        &::after {
          width: 53px;
        }
      }
    }
    @media (max-width: 480 + px) {
      font-size: rem(20);
      line-height: math.div(29, 20);
      font-weight: 600;
      margin-bottom: rem(15);
      ._accent {
        font-size: rem(21);
      }
    }
  }
  // .signup__acttion
  &__action {
    @include flex-center;
    column-gap: 82px;
    @include aminvalue("column-gap", 82, 15, 480);
    @include avalue("padding-bottom", 40, 28); // for drop-shadow
  }
  // .signup__signup-button
  &__signup-button {
    color: $white;
    background: linear-gradient(
      122.93deg,
      #7345d3 -137.34%,
      #5470f0 38.32%,
      #1858cd 129.02%
    );
    box-shadow: 0px 9px 19px rgba(0, 0, 0, 0.15);
    @media (max-width: $smallPhone + px) {
      box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.15);
    }
    border-radius: 60px;
    min-width: 180px;
    @media (max-width: 480 + px) {
      width: rem(203);
    }
  }
  ._dashed-line-left,
  ._dashed-line-right {
    width: pc(231, $maxWidthContainer);
    min-width: 231px;
    border-top: 1px dashed $mainBlue;
    height: 1px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 76px;
      top: 0;
      left: 0;
      transform: translateY(-76px);
      border-right: 1px dashed $mainBlue;
    }
    &::after {
      content: "";
      position: absolute;
      width: 100vw;
      bottom: 76px;
      transform: translateX(-100%);
      left: 0;
      height: 1px;
      border-top: 1px dashed $mainBlue;
    }
    ._gradient-dot {
      position: absolute;
      pointer-events: none;
      top: 50%;
      background: center / contain no-repeat var(--gradient-dot);
      @include size(11);
      transform: rotateY(180deg) translate(0, -50%);
    }
  }
  ._dashed-line-left {
    ._gradient-dot {
      right: -6px;
    }
  }
  ._dashed-line-right {
    &::before {
      left: auto;
      right: 0;
    }
    &::after {
      left: auto;
      right: 0;
      transform: translateX(100%);
    }
    ._gradient-dot {
      left: -6px;
    }
  }
}
.perk {
  position: relative;
  width: pc(450, $maxWidthContainer);
  @include aminvalue("padding-top", 36, 15, $phone);
  @include aminvalue("padding-bottom", 30, 15, $phone);
  @include aminvalue("padding-left", 28, 15, $phone);
  @include aminvalue("padding-right", 28, 15, $phone);
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
  backdrop-filter: blur(30px);
  border-radius: rem(15);
  @media (max-width: $phone + px) {
    width: pc(257, $phone);
  }
  @media (max-width: 700 + px) {
    width: 100%;
    padding: 10px;
  }
  @media (max-width: 700 + px) {
    display: flex;
    align-items: flex-start;
    max-width: 400px;
    margin-left: auto !important;
    margin-right: auto;
  }
  // .perk__icons
  &__icons {
    display: flex;
    justify-content: space-between;
    margin-bottom: rem(20);
    @media (max-width: $phone + px) {
      margin-bottom: rem(3);
    }
  }
  // .perk__icon
  &__icon {
    @media (max-width: $phone + px) {
      transform: scale(math.div(30, 42));
    }
    @media (max-width: 480 + px) {
      transform: scale(math.div(22, 42));
    }
  }
  // .perk__icon-shadow
  &__icon-shadow {
    width: 100%;
    flex: 0 0;
    transform: scale(math.div(84, 42));
    opacity: 0.04;
    @media (max-width: $phone + px) {
      transform: scale(math.div(44, 30));
    }
    @media (max-width: 700px) {
      position: absolute;
      top: 7%;
      right: 2%;
      transform: none;
      @include size(40);
    }
    @media (max-width: 480px) {
      @include size(30);
    }
  }
  // .perk__title
  &__title {
    @include aminvalue("font-size", 20, 14, $phone);
    line-height: math.div(30, 20);
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: rem(10);
    @media (max-width: $phone + px) {
      margin-bottom: rem(3);
    }
  }
  // .perk__text
  &__text {
    @include aminvalue("font-size", 18, 11, $phone);
    line-height: math.div(30, 18);
    font-weight: 300;
    ._accent {
      font-weight: 600;
      color: $purple;
    }
    ._accent-light {
      font-weight: 600;
      color: $lightBlue;
    }
  }
}
