.footer {
  color: $white;
  overflow-x: clip;
  overflow-y: visible;
  position: relative;
  margin-top: 1px;
  &::before {
    content: "";
    position: absolute;
    right: rem(-35);
    top: rem(-20);
    width: rem(67);
    height: rem(60);
    z-index: 1;
    background: center/contain no-repeat var(--hexagon-empty-thin-rounded);
    @media (max-width: $phone + px) {
      width: rem(53);
      height: rem(47);
      top: rem(-12);
      right: rem(55);
    }
    @media (max-width: $smallPhone + px) {
      top: rem(-26);
      right: rem(20);
    }
  }
  // .footer__upper-footer
  &__upper-footer {
  }
  // .footer__lower-footer
  &__lower-footer {
    height: rem(59);
    background-color: #06001d;
    @include flex-center;
  }
}
.upper-footer {
  position: relative;
  background: linear-gradient(
    219.43deg,
    #040114 24.07%,
    #080435 39.3%,
    #130f3d 79.8%,
    #000000 146.93%
  );
  &::before {
    content: "";
    display: block;
    width: 100%;
    height: rem(71);
    background: center top / cover no-repeat url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 1920 392"><path d="M0 0s584.101 70 960 70c375.9 0 960-70 960-70" fill="%23fff"/></svg>');
    transform: translateY(-1px);
    @media (max-width: $phone + px) {
      height: rem(22);
    }
    @media (max-width: $smallPhone + px) {
      height: rem(5);
    }
  }
  &::after {
    content: "";
    position: absolute;
    left: rem(-140);
    bottom: rem(24);
    width: rem(208);
    height: rem(178);
    background: center / contain no-repeat var(--hexagon-filled-squished);
    opacity: 0.15;
    transform: rotate(19.39deg) scale(1.1);
    @media (max-width: 900px) {
      display: none;
    }
  }
  &__container {
    display: flex;
    flex-wrap: wrap;
    gap: rem(40) rem(20);
    justify-content: space-between;
    padding-top: rem(83);
    padding-bottom: rem(83);
    &::before {
      content: "";
      position: absolute;
      right: rem(-110);
      bottom: rem(40);
      width: rem(208);
      height: rem(154);
      background: center / contain no-repeat var(--hexagon-filled-squished);
      opacity: 0.15;
      transform: rotate(19.39deg);
      @media (max-width: 900px) {
        width: rem(170);
        height: rem(126);
        transform: rotate(6.6deg);
        right: rem(-170 + 42);
        top: rem(30);
      }
    }
    &::after {
      content: "";
      position: absolute;
      width: rem(107);
      height: rem(96);
      left: rem(94);
      top: rem(-18);
      background: center / contain no-repeat var(--hexagon-empty-thin-rounded);
      @media (max-width: $phone + px) {
        display: none;
      }
    }
    & > * {
      @media (max-width: 1140 - 1px) {
        // flex-basis: calc(33.333% - 10px);
      }
      @media (max-width: $phone - 1 + px) {
        flex-wrap: wrap;
      }
    }
    @media (max-width: $phone + px) {
      padding-top: rem(40);
      padding-bottom: rem(25);
    }
    @media (max-width: $smallPhone + px) {
      gap: rem(20) rem(20);
      flex-direction: column;
      padding-left: rem(30);
      padding-right: rem(30);
      padding-top: rem(35);
      padding-bottom: rem(40);
    }
  }
  [class*="__title"] {
    font-size: rem(23);
    line-height: math.div(42, 23);
    margin-bottom: rem(20);
    font-weight: 600;
    @media (max-width: $phone + px) {
      font-size: rem(16);
    }
    @media (max-width: $phone + px) {
      margin-bottom: rem(10);
    }
  }
  [class*="__list"] {
    font-size: rem(18);
    line-height: math.div(42, 18);
    @media (max-width: $phone + px) {
      font-size: rem(14);
    }
  }
  // .upper-footer__logo
  &__logo {
    display: flex;
    flex-direction: column;
    gap: rem(29) rem(64);
    @media (max-width: 1140 - 1px) {
      flex-direction: row;
      flex: 0 0 100%;
    }
  }
  // .upper-footer__logo-link
  &__logo-link {
  }
  // .upper-footer__partnership-link
  &__partnership-link {
    @media (max-width: $phone + px) {
      width: auto;
    }
  }
  // .upper-footer__socials
  &__socials {
  }
  // .upper-footer__contacts
  &__contacts {
  }
  // .upper-footer__partners
  &__partners {
  }
}

.socials {
  // .socials__title
  &__title {
  }
  // .socials__list
  &__list {
    display: flex;
    gap: rem(10) rem(18);
    flex-wrap: wrap;
  }
  // .socials__list-item
  &__list-item {
  }
  // .socials__link
  &__link {
    width: rem(47);
    height: rem(47);
  }
}

.contacts {
  // .contacts__title
  &__title {
  }
  // .contacts__contact-list
  &__list {
  }
  // .contacts__item
  &__item {
    &:not(:last-child) {
      margin-bottom: rem(10);
    }
    @media (max-width: $smallPhone + px) {
      &:not(:last-child) {
        margin-bottom: rem(6);
      }
    }
  }
  &__link {
    display: flex;
    align-items: center;
    &::before {
      flex: 0 0 rem(26);
      font-size: rem(18);
      color: $mainBlue;
      margin-right: rem(15);
    }
  }
}
.partners {
  // .partenrs__header
  &__title {
  }
  // .partners__list
  &__list {
  }
  // .partners__list-item
  &__list-item {
  }
  // .partners__partner-link
  &__partner-link {
  }
}

.lower-footer {
  @media (max-width: $phone + px) {
    font-size: rem(13);
  }
  // .lower-footer__copyright
  &__copyright {
  }
}
