.plans {
  @include avalue("margin-top", 29, 10);
  overflow-x: visible;
  overflow-x: clip;
  // .plans__container
  &__container {
    max-width: rem(1920);
    overflow-x: clip;
    overflow-y: visible;
    @include flex-center;
    flex-direction: column;
    @media (max-width: 1220 + px) {
      padding: 0;
      width: 100%;
      max-width: none;
    }
    @media (max-width: 480px) {
      padding: 0;
    }
  }
  // .plans__header
  &__header {
    @include avalue("margin-bottom", 44, 18);
  }
  // .plans__title
  &__title {
    text-align: center;
    display: flex;
    align-items: center;
    @include avalue("height", 77, 39);
    ._decor-hexagon._left {
      &::before {
        transform: translate(-40%, -50%);
      }
    }
  }
  &__body {
    width: 100%;
    overflow-x: clip;
    overflow-y: visible;
    @media (min-width: 1220 + 1 + px) {
      max-width: 100%;
      max-width: $maxWidthContainer + 462 * 2 + 72 + px;
      display: flex;
      justify-content: center;
    }
  }
  // .plans__slider
  &__slider {
    position: relative;
    width: 100%;
    padding: 0 rem(15) rem(15);
    @media (min-width: 480 + px) {
      padding: 0 rem(15) rem(15);
    }
    @media (min-width: 900 + px) {
      padding: 0 rem(30) rem(30);
    }
    @media (min-width: 1140 + px) {
      padding: 0 rem(40) rem(40);
    }
    @media (min-width: 1220 + 1 + px) {
      width: rem((462 + 15 * 2) * 2 + 72 - 15);
    }
    @media (max-width: $phone + px) {
      width: 100%;
      overflow-x: clip;
      overflow-y: visible;
    }
    @media (max-width: 375px) {
      padding: 0;
    }
    .swiper-button-next,
    .swiper-button-prev {
      @include size(60);
      opacity: 0.9;
      @media (max-width: 1220 + px) {
        position: static;
        @include size(60);
        flex: 0 0 rem(60);
      }
      &::before {
        font-size: 26px;
      }
      @media (any-hover: hover) {
        &:hover {
          transform: translateY(-50%) scale(1.1);
        }
      }
    }
    .swiper-button-next {
      transform: translate(0, -50%);
      @media (max-width: 1220 + px) {
        transform: none;
      }
    }
    .swiper-button-prev {
      transform: translate(0, -50%);
      @media (max-width: 1220 + px) {
        transform: none;
      }
    }
    .swiper-slide {
      position: relative;
      opacity: 0.4;
      transition: opacity .3s;
      cursor: grab;
      &::before {
        // left: pc(304, 1041);
        left: 70%;
        @media (min-width: 480px) {
          content: "";
          position: absolute;
          bottom: -47px;
          // left: pc(52, 252);
          border: 1px dashed $mainBlue;
          border-top: none;
          height: 47px;
          // width: 443px;
          width: 70%;
        }
        @media (min-width: 480px) and (max-width: 900 - 1px) {
          height: 33px;
          width: 158px;
          bottom: -33px;
        }
      }
      &:last-of-type {
        &:before {
          display: none;
        }
      }
      &-active, &-next {
        opacity: 1;
      }
      }
  }
  // .plans__plan
  &__plan {
    flex: 0 0 100%;
    @media (min-width: 480 + px) {
      flex: 0 0 calc(50% - 7.5px);
    }
    @media (min-width: 900px) {
      flex: 0 0 calc(50% - 15px);
    }
    @media (min-width: 1140px) {
      flex: 0 0 calc(50% - 15px);
    }
  }
  // .plans__controls
  &__controls {
    @include avalue("margin-top", 70, 20);
    height: rem(26);
    @media (max-width: 1220 + px) {
      @include flex-center;
      column-gap: rem(60);
      height: rem(80);
    }
    @media (max-width: 480 + px) {
      column-gap: 0;
    }
  }
  // .plans__pagination
  &__pagination {
    @include flex-center;
    column-gap: rem(36);
    @media (max-width: 1220 + px) {
      column-gap: rem(40);
    }
    @media (max-width: 480 + px) {
      flex: 0 0 33%;
    }
    .swiper-pagination-bullet {
      @media (max-width: 480 + px) {
        display: none;
      }
      cursor: pointer;
      transition: background-color 0.3s, transform .3s;
      @include size(16);
      border-radius: 50%;
      background-color: $sliderLightButtonColor;
      box-sizing: border-box;
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.12);
      @media (any-hover: hover) {
        &:hover {
          background-color: rgba($sliderPaginationColor, 0.6);
        }
      }
      @media (max-width: $phone + px) {
        @include size(20);
      }
      &-active {
        background-color: $sliderPaginationColor;
        transform: scale(math.div(26, 16));
        @media (any-hover: hover) {
          &:hover {
            background-color: $sliderPaginationColor;
          }
        }
        @media (max-width: $phone + px) {
          transform: scale(math.div(30, 20));
        }
      }
    }
  }
}

.plan {
  color: $white;
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    197.97deg,
    #040114 -25.83%,
    #080435 -7.91%,
    #130f3d 12.04%,
    #221e48 82.1%,
    #000000 118.73%
  );
  border-radius: 15px;
  // .plan__additional-info
  &__additional-info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    column-gap: rem(15);
    @include aminvalue("padding-top", 25, 10, $phone);
    @include aminvalue("padding-right", 36, 14, $phone);
    @include aminvalue("padding-bottom", 0, 20, $phone);
    @include aminvalue("padding-left", 42, 14, $phone);
  }
  // .plan__payoff
  &__payoff {
    @include aminvalue("font-size", 15, 11, $phone);
    line-height: 1.3;
    font-weight: 300;
  }
  &__payoff-time {
    @include aminvalue("font-size", 28, 16, $phone);
    line-height: math.div(30, 28);
    font-weight: 500;
    margin-top: rem(6);
    display: inline-block;
  }
  // .plan__plan
  &__plan {
  }
  // .plan__timer
  &__timer {
  }
  // .plan__body
  &__body {
    @include aminvalue("padding-left", 43, 14, $phone);
    @include aminvalue("padding-right", 58, 14, $phone);
    @include aminvalue("padding-bottom", 42, 15, $phone);
    flex-grow: 1;
  }
  // .plan__image
  &__image {
    @include aminvalue("width", 181 + 35 * 2, 106 + 20 * 2, $phone);
    @include aminvalue("height", 173, 102, $phone);
    @include flex-center;
    margin: 0 auto rem(30);
    position: relative;
    background: center / contain no-repeat;
    @media (max-width: $phone + px) {
      margin-bottom: rem(15);
    }
    img {
      max-width: 100%;
      max-height: 100%;
    }
    ._gradient-dot {
      position: absolute;
      @include size(13);
      background: center / contain var(--gradient-dot);
      transform: rotate(-45deg);
      pointer-events: none;
      &:first-of-type {
        left: pc(7, 181);
        bottom: pc(35, 173);
      }
      &:last-of-type {
        right: pc(7, 181);
        top: pc(35, 173);
      }
      &::before,
      &::after {
        content: "";
        position: absolute;
        background: center / contain var(--gradient-dot);
        transform: rotate(-53deg);
      }
      &::before {
        @include size(5);
        right: pc(-4, 13);
        top: pc(-15, 13);
      }
      &::after {
        @include size(2);
        right: pc(-5, 13);
        bottom: pc(-0, 13);
      }
    }
  }
  // .plan__title
  &__title {
    @include aminvalue("font-size", 32, 16, $phone);
    line-height: math.div(37, 32);
    font-weight: 700;
    opacity: 0.94;
    position: relative;
    display: inline;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      @include aminvalue("width", 59, 36, $phone);
      @include aminvalue("height", 48, 29, $phone);
      // @include aminvalue("width", 48, 36, $phone);
      transform: translate(65%, -50%);
      z-index: -1;
      background: center / cover var(--hexagon-filled);
    }
  }
  // .plan__feature-list
  &__feature-list {
    @include aminvalue("padding-top", 30, 10, $phone);
    @include aminvalue("padding-bottom", 30, 10, $phone);
  }
  // .plan__feature-item
  &__feature-item {
    @include aminvalue("font-size", 19, 12, $phone);
    line-height: math.div(26, 19);
    letter-spacing: 0.01em;
    display: flex;
    @include aminvalue("column-gap", 24, 9, $phone);
    &:not(:last-child) {
      @include aminvalue("margin-bottom", 17, 8, $phone);
    }
    &::before {
      content: "";
      @include size(8);
      flex: 0 0 rem(8);
      margin-top: rem(10);
      background: center / cover var(--gradient-dot);
      transform: rotate(90deg);
      @media (max-width: $phone + px) {
        @include size(6);
        flex: 0 0 rem(6);
      }
    }
  }
  // .plan__footer
  &__prices {
    background: linear-gradient(147.07deg, #1673ff 50.02%, #5192e2 150.83%);
    @include aminvalue("height", 97, 72, $phone);
    border-radius: 0 0 rem(15) rem(15);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: rem(28);
    position: relative;
    @media (max-width: $phone + px) {
      padding-bottom: rem(23);
    }
  }
  // .plan__old-price
  &__old-price {
    color: $veryDarkBlue;
    @include aminvalue("font-size", 28, 20, $phone);
    line-height: 1;
    position: absolute;
    left: 5%;
    @media (max-width: $phone + px) {
      margin-left: rem(5);
    }
    &::before {
      content: "";
      width: 120%;
      height: 1px;
      position: absolute;
      bottom: 45%;
      left: 50%;
      transform: translateX(-50%);
      background-color: $veryDarkBlue;
    }
    .plan__per-month {
      @include aminvalue("font-size", 18, 10, $phone);
      @media (min-width: 480px) and (max-width: 900 - 1 + px) {
        display: none;
      }
    }
  }
  // .plan__per-month
  &__per-month {
    font-size: rem(18);
    font-weight: 500;
    @media (max-width: $phone + px) {
      font-weight: 400;
    }
  }
  // .plan__current-price
  &__current-price {
    @include aminvalue("font-size", 56, 32, $phone);
    line-height: 1;
    font-weight: 500;
    @media (max-width: $phone + px) {
      margin-bottom: rem(-4);
      margin-left: rem(15);
      .plan__per-month {
        margin-left: rem(-5);
      }
    }
  }
}
.timer {
  padding: rem(12) rem(15);
  @include aminvalue("font-size", 16, 10, $phone);
  // @include aminvalue("line-height", math.div(26, 16), math.div(26, 10), $phone);
  line-height: 1;
  font-weight: 500;
  border: 2px solid $mainBlue;
  box-sizing: border-box;
  filter: drop-shadow(0px 9px 19px rgba(0, 0, 0, 0.15));
  border-radius: 60px;
  text-align: center;
  white-space: nowrap;
  @media (max-width: $phone + px) {
    padding: rem(8) rem(10) rem(8) rem(6);
  }
  &__days,
  &__hours,
  &__minutes,
  &__seconds {
    @include aminvalue("font-size", 14, 8, $phone);
    @include avalue("width", 17, 9);
    text-align: right;
    display: inline-block;
  }
  // .timer__days
  &__days {
  }
  // .timer__hours
  &__hours {
  }
  // .timer__minutes
  &__minutes {
  }
  // .timer__seconds
  &__seconds {
  }
}
